<template>
    <div class="not-found-page">
        <van-button
          plain
          class="back-home-btn"
          @click="goHome"
          >返回首页
        </van-button>
    </div>
</template>

<script>
import { defineComponent } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
  setup() {
    const router = useRouter();
    const goHome = () => {
        router.push({
            path: "/"
        })
    };
    return {
      goHome,
    };
  },
});
</script>


<style scoped lang="scss">
.not-found-page {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(../../assets/image/404.png);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  .back-home-btn {
    background: rgb(77, 77, 77);
    color: #fff;
    border-color: #aaa;
    border-radius: 25px;
    width: 100px;
    height: 30px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 50px;
  }
}
</style>